body {
  background-color: #d5d5d5;
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


.Game {
  background-color: #ccc;
  text-align: center;
  display: -webkit-flex;
  display: flex;
  margin-top: 20px;
  -webkit-justify-content: center;
          justify-content: center;
}

.Game-logo {
  -webkit-animation: Game-logo-spin infinite 20s linear;
          animation: Game-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.Game-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.Game-link {
  color: #61dafb;
}

@-webkit-keyframes Game-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes Game-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.Logs {
  white-space: pre-line;
  margin-top: 20px;
  text-align: center
}
.hero__container {
    display: grid;
    grid-template-columns: 50px 50px 50px;
}

.hero__container img {
    height: 50px;
    width: 50px;
    border: 1px solid lightBlue;
}

.hero__hp-bar {
    width:150px;
    height:20px;
    border: 1px solid black;
    position: absolute;
}

.common {background-color: #666;}
.uncommon {background-color: green;}
.rare {background-color: blue;}
.heroic {background-color: purple;}
.epic {background-color: yellow;}
.legendary {background-color: orange;}
.relict {background-color: red;}
.control-container{
    margin-left: 20px;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: center;
            justify-content: center;
}

.control-container label{
    display: block;
    text-align: left;
}
.control-box__button{
    display: grid;
    justify-items: center;
}

.control-box{
    display: -webkit-flex;
    display: flex;
}

button {
    height: 75px;
    width: 200px;
    background-color: #666;
    border: 1px solid lightblue;
    border-radius: 4px;
    font-size: 16px;
    font-weight: bold;
    color: lightblue;
}

